import React, {FC} from "react"

import {
    Box, Grid, Typography,
    Button,
} from '@mui/material';
import {
    ShoppingBasket as ShoppingBasketIcon
} from "@mui/icons-material";

import Gallery from "./Gallery";
import {IProduct} from "../../../types";
import VariantList from "./VariantList";
import Price from "./Price";
import AttributeList from "./AttributeList";

interface ICProps extends IProduct {
}

const variants = [
    {
        name: '',
        unit: '.',
        options: ['17', '17.5', '18',]
    },
    {
        name: '',
        unit: '',
        options: ['', '']
    },
    {
        name: '',
        unit: '',
        options: ['750', '925']
    },
]

const attributes = [
    {
        name: '',
        icon: 'AutoAwesome',
        unit: '',
        options: [
            ''
        ]
    },
    {
        name: '',
        icon: 'HighQuality',
        unit: '',
        options: [
            '925'
        ]
    },
    {
        name: '',
        icon: 'Balance',
        unit: '.',
        options: [
            '1.84'
        ]
    },
    {
        name: '',
        icon: 'AutoAwesome',
        unit: '.',
        options: [
            '17',
            '17.5',
            '18',
        ]
    },
]
const Product: FC<ICProps> = (props) => {

    const images = props.img.map(e => ({
        ...e,
        src: `${process.env.PUBLIC_URL}/images/products/${e.src}`
    }))
    return (
        <Grid container columnSpacing={{xs: 1}}>
            <Grid item xs={12} sm={7}>
                <Gallery items={images}/>
            </Grid>
            <Grid item xs={12} sm={5}>
                <Typography variant="h3" component="h2">{props.name}</Typography>
                <Typography variant="subtitle2" component="p" pb={1}>: {props.name}</Typography>
                <Typography variant="subtitle1" component="p" pb={2}>{props.description}</Typography>
                <Box pb={2} sx={{display: 'flex', justifyContent: 'space-between'}}>
                    <Price price={125.25} discount={10} unit={'.'}/>
                    <Button
                        variant="outlined"
                        startIcon={<ShoppingBasketIcon/>}
                        size="large"
                    >
                        
                    </Button>

                </Box>
                <Typography variant="h6" component="p" pb={1}></Typography>
                <VariantList items={variants} pb={1}/>
                <Typography variant="h6" component="p" pb={2}></Typography>
                <AttributeList items={attributes}/>
            </Grid>
        </Grid>
    )
}

export default Product
